.gallery {
  position: fixed;
  inset: 0;
  z-index: 2;
}

.gallery-img {
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  position: absolute;
}
.gallery-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.skrim {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.stage {
  position: absolute;
  inset: 0;
}

.arrow-left,
.arrow-right {
  width: 3rem;
  height: 3rem;
  background-color: var(--light-blue);
  border-radius: 50%;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  margin: 1rem;
  opacity: 75%;
  transition: opacity 200ms ease-out;
}
.arrow-left:hover,
.arrow-right:hover {
  opacity: 100%;
}

.arrow-left {
  left: 0;
}

.arrow-right {
  right: 0;
}

:root {
  --t-time: 300ms;
  --easing: ease-out;
}

@media (prefers-reduced-motion: reduce) {
  :root {
    --t-time: 0ms;
  }
}

.start-left {
  transform: translateX(-20%);
}

.start-right {
  transform: translateX(20%);
}

.start-up {
  transform: translateY(-20%);
}

.start-down {
  transform: translateY(20%);
}

.start-out {
  opacity: 0;
}

.start-in {
  opacity: 1;
  transform: translate(0, 0);
}

.transition-out-up {
  transition: opacity var(--t-time) var(--easing), transform var(--t-time) var(--easing);
  transform: translateY(-20%);
  opacity: 0;
}

.transition-out-down {
  transition: opacity var(--t-time) var(--easing), transform var(--t-time) var(--easing);
  transform: translateY(20%);
  opacity: 0;
}

.transition-out-left {
  transition: opacity var(--t-time) var(--easing), transform var(--t-time) var(--easing);
  transform: translateX(-20%);
  opacity: 0;
}

.transition-out-right {
  transition: opacity var(--t-time) var(--easing), transform var(--t-time) var(--easing);
  transform: translateX(20%);
  opacity: 0;
}

.transition-in-center {
  transition: opacity var(--t-time) var(--easing), transform var(--t-time) var(--easing);
  transform: translate(0, 0);
  opacity: 1;
}
